      <button class="add-btn animated zoomInRight" @click="$router.push('/buy/stepFirst')">开始添加</button>
    </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  components: {
  },
  created () {
  },
  methods: {
  }
}
</script>

<style scoped lang="scss">
@import '@/assets/css/animate.scss';
@import '@/assets/css/color.scss';
.detail-container{
  padding-top: 40px;
  p{
    font-size: 16px;
    line-height: 30px;
  }
  .add-ad{

  }
  .step1{
    margin-top: 20px;
    color: $themeColor;
  }
  .step2{
    margin: 20px 0;
  }
  .add-btn{
    display: inline-block;
    padding: 10px 20px;
    font-size: 12px;
    border-radius: 8px;
    color: #fff;
    outline: none;
    background-color: #19be6b;
    border-color: #19be6b;
    border: none;
    transition: all .2s;
    &:active{
      background: #47cb89;
    }
  }
}
</style>



